@font-face {
    font-family: 'Pointless';
    src: local('Pointless'), url('./Fonts/Pointless/Pointless.ttf') format('truetype');
    font-weight: normal;
}

.sponsors {
    background-color: #C59CFF;
    height: auto;
    width: 100%;
    position: relative;
    padding: 10%;
    box-sizing: border-box; /* Include padding in the total width/height */
}

.sponsors .container {
    display: flex;
    justify-content: flex-start; /* Align content to the start (left) */
    align-items: flex-start; /* Align items to the start (top) */
    height: 100%;
    padding: 30px; /* Add padding of 30px */
    box-sizing: border-box; /* Include padding in the total width/height */
}

h1 {
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-size: 50px;
}

h2 {
    font-family: 'Pointless', sans-serif;
    margin-bottom: 10px;
}

.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 3; /* Set a higher z-index for the header */
}

.carousel-container {
    margin-top: 120px; /* Adjusted margin to create space below the fixed header */
    margin-bottom: 50px;
    position: relative;
    z-index: 2; /* Set a lower z-index for the carousel */
}


.mySwiper {
    width: 100%;
}

.swiper-slide {
    background-position: center;
    background-size: cover;
    width: 200px;
    height: 200px;
    margin: 5px;
    transition: transform 0.3s ease;
}

.swiper-slide.swiper-slide-active {
    transform: scale(1.2) rotateY(0deg);
}

.swiper-slide-prev, .swiper-slide-next {
    transform: scale(0.8) rotateY(45deg);
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
}

.swiper-image-container {
    position: relative;
}

.image-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #1C0028;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.swiper-image-container:hover .image-overlay {
    opacity: 1;
}

.image-overlay p {
    color: #1C0028;
    font-size: 16px;
    font-weight: bold;
    text-align: center;
    margin: 0;
}

.image-overlay a {
    color: inherit;
    text-decoration: none;
}

.image-overlay a:hover {
    text-decoration: underline;
}
